import {mapState} from "vuex"
export default {
	data() {
		return {
			list:[]
		};
	},
	computed:{
		...mapState(["searchList"])
	},
	created(){
		let idList = []
		Object.values(this.searchList).forEach(i=>{
			if(!idList.includes(i.id)){
				idList.push(i.id)
				this.list.push(i)
			}
		})
	},
	methods:{
		//返回
		back(){
			uni.navigateBack()
		},
		//跳转详情页
		toPath(item){
			uni.showLoading({
				title:"搜索中。。。"
			})
			uni.navigateTo({
				url:`/pages/item/item?id=${item.id}`,
				success:()=>{
					uni.hideLoading()
				}
			})
		}
	}
}
